<template>
    <div class="manual-up-down">
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>手动上下分</span>
                </div>
            </div>
            <div class="data-con">
                <div class="data-list">
                    <div class="data-item">
                        <span>用户名</span>
                        <Select v-model="username" filterable class="column-input">
                            <Option v-for="(item, index) in userList" :value="item.id" :key="index">{{ item.title }}</Option>
                        </Select>
                        <i-select v-model="upDown" class="item-select">
                            <i-option v-for="(item, index) in upDownList" :key="index" :value="item.key">{{item.title}}</i-option>
                        </i-select>
                        <InputNumber :min="0" v-model="upDownScore" @on-focus="handleFocus($event)" class="column-input" placeholder="输入处理金额(正数)"></InputNumber>
                        <span>备注</span>
                        <Input v-model="remark" placeholder="备注信息" class="column-input"></Input>
                        <div class="operate" @click="handleSubmit">确定</div>
                    </div>
                </div>
            </div>
        </div>
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>上下分记录</span>
                    <div class="list-poptip">
                        <span>上分合计：</span>
                        <span>{{allUp}}</span>
                        <span>下分合计：</span>
                        <span>{{allDown}}</span>
                    </div>
                </div>
            </div>
            <div class="query-list">
                <span>时间：</span>
                <DatePicker v-model="searchDate" type="daterange" format="yyyy-MM-dd" placeholder="选择搜索的时间" class="search-date"></DatePicker>
                <i-select v-model="recordType" class="item-select">
                    <i-option v-for="(item, index) in recordTypeList" :key="index" :value="item.key">{{item.title}}</i-option>
                </i-select>
                <Input v-model="searchName" placeholder="用户ID/用户名" class="column-input" @on-blur="handleBlur"></Input>
                <div class="search-button" @click="handleSearch">
                    <span>查询</span>
                </div>
            </div>
            <div class="data-con">
                <div class="data-header">
                    <p class="item-info">用户名</p>
                    <p class="item-info">ID</p>
                    <p class="item-info">申请类别</p>
                    <p class="item-info">金额</p>
                    <p class="item-info">申请时间</p>
                    <p class="item-info">备注</p>
                    <p class="item-info">状态</p>
                </div>
                <div class="data-list">
                    <div class="data-item1" v-for="(item, index) in allUpDownList" :key="index">
                        <p class="item-info">{{item.nickname}}</p>
                        <p class="item-info">{{item.showId}}</p>
                        <p class="item-info">{{item.status}}</p>
                        <p class="item-info">{{item.money}}</p>
                        <p class="item-info">{{item.time}}</p>
                        <p class="item-info" :title="item.remark">{{item.remark}}</p>
                        <p class="item-info">{{item.operate}}</p>
                    </div>
                    <div v-if="!allUpDownList.length" class="no-data">
                        <span>暂无数据</span>
                    </div>
                </div>
            </div>
            <Page class="footer-page" :total="page.total" :page-size="page.pageSize" :current="page.pageNum" show-sizer @on-page-size-change="handleChangeSize" @on-change="handleChangePage"/>
        </div>
        <!-- <loading :loading="showLoading"></loading> -->
    </div>
</template>

<script>
export default {
    data() {
        return {
            showLoading: false,
            page: {
                total: 0,
                pageSize: 10,
                pageNum: 1
            },
            searchName: '',
            recordType: 0,
            recordTypeList: [
                {
                    key: 0,
                    title: '所有记录'
                },
                {
                    key: 1,
                    title: '上分(申请)'
                },
                {
                    key: 2,
                    title: '下分(申请)'
                },
                {
                    key: 3,
                    title: '上分(手动)'
                },
                {
                    key: 4,
                    title: '下分(手动)'
                }
            ],
            searchDate: [new Date(), new Date(new Date().setDate(new Date().getDate() + 1))],
            remark: '',
            username: '',
            upDown: 1,
            allUp: 0,
            allDown: 0,
            allUpDownList: [],
            upDownList: [
                {
                    key: 1,
                    title: '加款'
                },
                {
                    key: 2,
                    title: '扣款'
                }
            ],
            userList: [],
            upDownScore: 0,
            isLoading1: false
        }
    },
    mounted() {
        let that = this;

        that.getUserList();
        that.getData();
        that.getDataTotal();
    },
    methods: {
        handleBlur() {
            let that = this;

            if (that.searchName) {
                that.page.pageNum = 1;
            }
        },
        handleFocus(e) {
            e.target.select();
        },
        getDataTotal() {
            let that = this,
                vParam = {};

            if (that.recordType) {
                vParam['status'] = that.recordType;
            }
            vParam['startTime'] = that.searchDate[0].pattern('yyyy-MM-dd') + ' 00:00';
            vParam['endTime'] = that.searchDate[1].pattern('yyyy-MM-dd') + ' 23:59';

            that.$axios({
                url: '/api/Setup/GetTotalIntegral',
                params: vParam
            }).then(data => {
                if (data.data.Status === 100) {
                    const result = data.data.Data;

                    that.allUp = result.UpScore;
                    that.allDown = result.DownScore;
                }
            })
        },
        handleChangeSize(_pageSize) {
            let that = this;

            that.page.pageSize = _pageSize;
            that.page.pageNum = 1;
            that.getData();
            that.getDataTotal();
        },
        handleChangePage(page) {
            let that = this;

            that.page.pageNum = page;
            that.getData();
            that.getDataTotal();
        },
        handleSearch() {
            let that = this;

            that.getData();
            that.getDataTotal();
        },
        handleSubmit() {
            let that = this,
                href = '';

            if (!that.username) {
                that.$Message.error('请输入用户名');
                return;
            }

            if (!that.upDownScore) {
                that.$Message.error('请输入处理金额');
                return;
            }

            if (!that.remark) {
                that.$Message.error('请输入备注');
                return;
            }

            switch(that.upDown) {
                case 1:
                    href = '/api/Setup/ManualUpper';
                break;
                case 2:
                    href = '/api/Setup/ManualLower';
                break;
            }

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: href,
                    method: 'post',
                    params: {
                        userID: that.username,
                        branch: that.upDownScore,
                        remark: that.remark
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.username = '';
                    that.upDownScore = 0;
                    that.remark = '';
                    that.isLoading = false;
                    that.getData();
                    that.getDataTotal();
                })
            }
        },
        getUserList() {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: '/api/Setup/GetUpDownUserList',
                    params: {
                        status: true
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.userList = result.map(item => {
                            return {
                                    title: item.NickName + '(' + item.OnlyCode + ')',
                                    id: item.UserID
                                }
                        })
                    }
                    that.isLoading = false;
                })
            }
        },
        getData() {
            let that = this,
                param = {};

            if (!that.searchDate.length) {
                that.$Message.error('请选择日期');
                return;
            }

            param['startTime'] = that.searchDate[0].pattern('yyyy-MM-dd') + ' 00:00';
            param['endTime'] = that.searchDate[1].pattern('yyyy-MM-dd') + ' 23:59';
            if (that.recordType) {
                param['status'] = that.recordType;
            }
            param['start'] = that.page.pageNum;
            param['pageSize'] = that.page.pageSize;
            if (that.searchName) {
                param['keyword'] = that.searchName;
            }

            if (!that.isLoading1) {
                that.isLoading1 = true;
                that.showLoading = true;

                that.$axios({
                    url: '/api/Setup/GetIntegralRecord',
                    params: param
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.page.total = data.data.Total;
                        that.allUpDownList = result.map(item => {
                            return {
                                id: item.ID,
                                nickname: item.NickName,
                                loginName: item.LoginName,
                                money: item.Amount,
                                time: item.ApplyTime.substr(0, 16),
                                status: item.Status + (item.OrderStatus.substr(0,2) === '申请' ? (item.OrderStatus.substr(2,2) + '(申请)') : ((item.OrderStatus.substr(0,2) === '充值' ? '上分' : '下分') + '(手动)')),
                                operate: item.Management,
                                remark: item.Remark,
                                showId: item.OnlyCode
                            }
                        })
                    }
                    that.isLoading1 = false;
                    that.showLoading = false;
                })
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.manual-up-down {
    position: relative;
    .content {
        .list-header {
            width: 100%;
            .list-title {
                justify-content: space-between;

                width: 100%;
                .list-poptip {
                    font-weight: 400;

                    & > span:nth-child(2n+2) {
                        display: inline-block;

                        padding-left: 5px;

                        min-width: 95px;
                    }

                    & > span:nth-child(2) {
                        color: rgb(0, 102, 102);
                    }

                    & > span:nth-child(4) {
                        color: rgb(255, 51, 0);
                    }
                }
            }
        }
        .query-list {
            display: flex;
            align-items: center;

            margin-top: 10px;
            padding: 0 15px;

            height: 60px;

            border-radius: 2px;
            background-color: rgb(228, 228, 228);
            border: 1px solid rgb(215, 215, 215);

            & > * {
                flex-shrink: 0;
            }

            .search-date {
                width: 260px;
            }

            .item-select {
                margin-left: 35px;

                width: 170px;

                text-align: left;
            }

            .column-input {
                margin-left: 10px;

                width: 190px;
            }

            .search-button {
                margin-left: 18px;

                width: 150px;
                height: 30px;
                line-height: 26px;

                // background-color: rgb(204, 204, 204);
                // border-radius: 4px;
                // border: solid 1px rgb(161, 161, 161);
                background-color: #0078ff;
                box-shadow: inset 0px 0px 5px 0px rgba(124, 183, 255, 0.75);
                border-radius: 10px;
                border: solid 1px #0e3c90;
                text-align: center;
                cursor: pointer;
                color: #fff;
            }
        }
        .data-con {
            margin-top: 10px;

            .data-header, .data-item1 {
                display: flex;
                align-items: stretch;

                height: 40px;

                .item-info {
                    flex-shrink: 0;

                    width: 133px;
                    line-height: 40px;

                    border-right: solid 1px #d7d7d7;
                    border-bottom: solid 1px #d7d7d7;
                }

                .item-info:last-child {
                    border-right: 0;
                }
            }

            .data-header {
                background-color: rgb(228, 228, 228)
            }

            .data-list {

                .data-item1 {
                    height: 30px;

                    background-color: rgb(249, 249, 249);

                    .item-info {
                        line-height: 30px;

                        word-break: keep-all;
                        white-space: nowrap;
                    }

                    .item-info:nth-child(6) {
                        overflow: hidden;
                        font-size: 13px;
                        cursor: help;
                    }
                }

                .data-item1:last-child {
                    .item-info {
                        border-bottom: 0;
                    }
                }

                .data-item {
                    display: flex;
                    align-items: center;

                    height: 30px;

                    & > span {
                        flex-shrink: 0;

                        padding: 0 10px 0 5px;
                    }

                    .column-input {
                        flex-shrink: 0;

                        width: 144px;
                    }

                    .item-select {
                        flex-shrink: 0;

                        margin-left: 33px;

                        width: 86px;

                        text-align: left;
                    }

                    .column-input:nth-child(4) {
                        margin-left: 10px;

                        width: 145px;
                    }

                    & > span:nth-child(5) {
                        padding-left: 30px;
                    }

                    .column-input:nth-child(6) {
                        margin-right: 18px;

                        width: 240px;
                    }

                    .operate {
                        width: 122px;
                        height: 30px;
                        line-height: 26px;

                        background-color: #11ad44;
                        box-shadow: inset 0px 0px 5px 0px rgba(124, 255, 158, 0.75);
                        border-radius: 10px;
                        border: solid 1px #0e903c;
                        text-align: center;
                        cursor: pointer;
                        color: #fff;
                    }
                }
            }
        }
    }

    .content:nth-child(2) {
        .data-con {
            border: solid 1px #bcbcbc;
            overflow: hidden;

            .data-list {
                min-height: 300px;

                background-color: rgb(249, 249, 249);
            }
        }
    }
}
</style>